
// jQuery UI styles, borrowed from them
.ui-progressbar {
	position: relative;
    top: 130px;
    height: 2em;
	text-align: left;
	overflow: hidden;
    }

.progress-label {
	position: relative;
	top: 4px;
	left: 45%;
    font-weight: bold;
	text-shadow: 1px 1px 0 #fff;
	color: black;	
    }

.ui-progressbar .ui-progressbar-value {
	margin: -23px -1px;
	height: 100%;
	background-color: #eee;
	}

.ui-progressbar .ui-progressbar-overlay {
	background: url("data:image/gif;base64,");
	height: 100%;
	filter: alpha(opacity=25); /* support: IE8 */
	opacity: 0.25;
}

.ui-progressbar-indeterminate .ui-progressbar-value {
	background-image: none;
}

#page {
	display: none;
}

#draggable:hover {
	cursor: not-allowed; 
}
